<template>
  <div id="musicPlayer">
    <audio
      @timeupdate="ontimeupdate"
      id="audio"
      :src="this.$store.state.musicSrc"
      style="width: 500px;  margin: 0 auto;   display: block;background: aqua; "
      autoplay
    ></audio>

    <div id="btn" class="left">
      <a href="javascript:;" id="play" @click="play" v-show="btn">
        <img src="../assets/播放.png" alt="播放" />
      </a>
      <a href="javascript:;" id="pause" @click="pause" v-show="!btn">
        <img src="../assets/暂停.png" alt="暂停" />
      </a>
    </div>
    <!-- 进度条 -->
    <div id="time" class="left">
      <!-- 歌曲名 -->
      <div id="currentMusic">
        <a href="javascript:;">
          <span>♬{{ $store.state.currentMusic.name }}－</span>
        </a>

        <a href="javascript:;">
          <span>{{ artistsName }}</span>
        </a>
      </div>
      <div id="progressBar" style="cursor:pointer" @click="clickProgressBar($event)">
        <div id="plannedSpeed"></div>
      </div>
      <div id="timeDisplay">
        <!-- 显示时间 -->
        <span id="currentTime">{{$store.state.currentTime}}</span>/
        <span id="duration">{{$store.state.duration}}</span>
      </div>
    </div>
    <div class="volume">
      <input id="volume" type="range" min="0" max="100" value="50" @input="densityInput($event)" />
    </div>
    <div id="musicCover">
      <img :src="$store.state.musicCover" />
    </div>
  </div>
</template>

<script>
export default {
  name: "musicPlayer",
  mounted() {
    this.audio = document.getElementById("audio");
    this.plannedSpeed = document.querySelector("#plannedSpeed");
    this.progressBar = document.querySelector("#progressBar");
    this.$store.state.audio = document.getElementById("audio");
  },
  computed: {},
  data() {
    return {
      audio: {},
      duration: "",
      plannedSpeed: "",
      progressBar: "",
      btn: true,
      artistsName: ""
    };
  },
  methods: {
    //音量
    densityInput: function(event) {
      console.log(event);
      this.audio.volume = event.target.value / 100;
    },
    //播放
    play: function() {
      this.audio.play();
      this.btn = !this.btn;
    },
    //暂停
    pause: function() {
      this.audio.pause();
      this.btn = !this.btn;
    },
    //时间改变时
    ontimeupdate: function() {
      let that = this;
      this.$options.methods.myFunction(that);
      this.$options.methods.plannedChange(that);
      if (!this.audio.paused) {
        this.btn = false;
      }
      this.artistsName = this.$store.state.currentMusic.artists[0].name;
    },
    //时间改变
    myFunction: function(that) {
      let currentTime = that.audio.currentTime;
      that.$store.commit("time", {
        duration: currentTime,
        msg: "currentTime"
      });
      //修改音乐时长
      that.$store.commit("time", {
        duration: that.$store.state.audio.duration,
        msg: "duration"
      });
    },
    //进度条改变
    plannedChange: function(that) {
      that.plannedSpeed.style.width =
        (that.audio.currentTime / that.audio.duration) * 100 + "%";
    },
    //点击进度条
    clickProgressBar: function(evnet) {
      console.log(evnet);
      let x = evnet.offsetX / this.progressBar.offsetWidth;
      this.plannedSpeed.style.width = x * 100 + "%";
      this.audio.currentTime = this.audio.duration * x;
      this.audio.play();
    }
  }
};
</script>


<style scoped>
.left {
  float: left;
}
* {
  color: rgba(225, 225, 225, 0.8);
  margin: 0;
  padding: 0;
}
#musicPlayer {
  width: 80%;
  height: 80px;
  background-color: rgba(0, 0, 0, 0.03);
  margin: 0 auto;
  border-radius: 10px;
}
#time {
  position: relative;
  width: 600px;
  height: 80px;
}
#progressBar {
  margin-top: 10px;
  position: relative;
  /* border-radius: 15px; */
  height: 3px;
  /* background-color: rgba(255, 255, 255, 0.8); */
  border-top: rgba(255, 255, 255, 0.4) 2px solid;
  width: 80%;
  float: left;
  top: 5%;
  margin-right: 15px;
}

#plannedSpeed {
  top: -2.6px;
  position: absolute;
  border-radius: 14px;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.8 );
  width: 0%;
  /* display: inline-block; */
  color: #777777;
}

#progressBar:hover #plannedSpeed {
  background-color: rgba(248, 248, 248, 1);
}
#timeDisplay {
  position: absolute;
  top: 40%;
  display: inline-block;
}
#btn {
  width: 100px;
  height: 80px;
  position: relative;
}
#btn a {
  left: 50%;
  top: 50%;
  position: absolute;
  display: block;
  height: 25px;
  width: 25px;
  transform: translate(-50%, -50%);
}
#btn a img {
  height: 25px;
  width: 25px;
}
.volume {
  width: 150px;
  height: 80px;
  position: relative;
  /* top: 45%; */
  float: left;
}
#volume {
  position: absolute;
  top: 40%;
}
#currentMusic {
  width: 450px;
  /* margin-top: 10px; */
  font-size: 12px;
  margin: 10px 0 0 50px;
}
#currentMusic span:hover {
  color: rgba(225, 225, 225, 1);
}
#musicCover {
  float: left;
  width: 200px;
  height: 80px;
}
#musicCover img {
  border-radius: 50%;
  width: 80px;
  height: 80px;
}
</style>