import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    audio: '',//播放器
    search: [],
    musicSrc: '',//歌曲url
    currentTime: '00:00',//当前时间
    duration: '00:00',//总时间
    currentMusic:{},//当前播放歌曲信息
    musicCover:"http://pic.netbian.com/uploads/allimg/161015/233452-1476545692a380.jpg",
    review:undefined,//评论
  },
  mutations: {
    time: function (state, value) {
      if (isNaN(value.duration)) {
        value.duration = 0
      }
      // console.log(value, msg)
      value.duration = Math.floor(value.duration)
      let a
      let b
      let msg = value.msg
      a = Math.floor(value.duration / 60)
      b = value.duration % 60
      state[msg] = a.toString().padStart(2, "0") + ":" + b.toString().padStart(2, "0")
    }
  },
  actions: {

  },
  modules: {

  }
})
