<template>
  <div id="body">
    <div id="nav">
      <nav>
        <ul>
          <li>
            <a href="#">Logo</a>
          </li>
          <li>
            <router-link to="/">播放列表</router-link>
          </li>
          <li>
            <router-link to="/about">歌词</router-link>
          </li>
          <div>
            <input
              class="searchBar"
              @keyup.enter="investigation"
              type="text"
              v-model="query"
              placeholder="搜索音乐"
            />
            <a href="javascript:;" @click="investigation">
              <img src="../src/assets/searchBar.png" alt />
            </a>
          </div>
        </ul>
      </nav>
      <router-view></router-view>
      <div></div>
      <musicPlayer id="musicPlayer"></musicPlayer>
    </div>
  </div>
</template>



<script>
import musicPlayer from "./components/musicPlayer";
export default {
  name: "app",
  components: {
    musicPlayer
  },
  data() {
    return {
      query: ""
    };
  },
  mounted() {},
  methods: {
    investigation: function() {
      //搜索歌曲 axios
      this.axios
        .get("https://autumnfish.cn/search?keywords=" + this.query)
        .then(
          response => {
            // console.log(this.$store.state.search);
            this.$store.state.search = response.data.result.songs;
            // console.log(response.data.result.songs);
            // this.search = response.data.result.songs;
            // console.log(this.$store.state.search);
          },
          error => {
            console.log(error);
          }
        );
    }
  }
};
</script>




