<template>
  <div class="playlist">
    <ul>
      <li>
        <span>歌曲</span>
        <span>歌手</span>
        <span>专辑</span>
      </li>
      <li v-for="item in this.$store.state.search" :key="item.id" @dblclick="playMusic(item)">
        <span>{{item.name}}</span>
        <span>{{item.artists[0].name}}</span>
        <span>{{item.album.name}}</span>
        <div id="playMusic">
          <a href="javascript:;" @click="playMusic(item)">
            <img src="../assets/play.png" />
          </a>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  data() {
    return {
      // search:this.$store.state.search,
    };
  },
  components: {},
  methods: {
    playMusic: function(item) {
      //播放歌曲
      //获取歌曲链接
      this.axios.get("https://autumnfish.cn/song/url?id=" + item.id).then(
        response => {
          this.$store.state.musicSrc = response.data.data[0].url;
          this.$store.state.currentMusic = item;
          // console.log( this. $store.state.currentMusic);
          //修改背景
          // event.path[2].style.backgroundColor = "rgba(0, 0, 0, 0.1)";
        },
        error => {
          console.log(error);
        }
      );

      //获取歌曲封面
      this.axios
        .get("https://autumnfish.cn/song/detail?ids=" + item.id)
        .then(response => {
          // console.log(response)
          // console.log(response.data.songs[0].al.picUrl)
          this.$store.state.musicCover = response.data.songs[0].al.picUrl;
        });
        //获取评论
        this.axios.get("https://autumnfish.cn/comment/hot?type=0&id="+item.id).then(response =>{
          console.log(response)
          this.$store.state.review = response
        })
    }
  }
};
</script>

